import { _decorator, Component, EditBox, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('registerClient')
export class registerClient extends Component {

    @property(EditBox)
    usernameregisterInput: EditBox | null = null;

    @property(EditBox)
    passwordregisterInput: EditBox | null = null;

    private socket: any; // 使用 any 类型以避免类型错误

    start() {
        // 使用全局变量 io 进行连接
        this.socket = (window as any).io('http://localhost:3000');
    }

    update(deltaTime: number) {

    }

    onRegisterButtonClicked() {
        if (this.usernameregisterInput && this.passwordregisterInput) {
            const username = this.usernameregisterInput.string;
            const password = this.passwordregisterInput.string;
            console.log('用户名', username);
            console.log('密码', password);

            // 通过 Socket.io 发送登录请求
            this.socket.emit('login', { username, password });
        }
    }
    
}


